import React from 'react';
import Tilt from 'react-tilt';
import './Logo.css'
import faceReg from './facial-recognition.png'


const Logo = () => {
    return (
        <div className='ma4 mt0'>
            <Tilt className="Tilt br2 shadow-2" options={{ max : 55 }} style={{ height: 150, width: 150 }} >
                <div className="Tilt-inner pa3"><img style={{paddingTop: '1px'}} src={faceReg} alt='logo'/></div>
            </Tilt>
        </div>
    )
}

export default Logo
